import React, { Component } from "react";
import { Link } from "react-router-dom";

// Core Components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";

export default class ShipList extends Component {
  render() {
    const azurAPI = this.props.azurAPI;
    return (
      <>
        <CSNavbar />
        <div className="wrapper">
          <div className="page-header shipheader">
            <div className="content">
              <h1>Ship List</h1>
              <div className="containerCS">
                {azurAPI.map((ship) => {
                  const shipLink = encodeURIComponent(ship.names.en.replace(/\s+/g, "_"));
                  return (
                    <div className="cardCS shipCard" key={ship._gid}>
                      <Link to={`/ship/${shipLink}`} className="shipName linkCS">
                        {ship.names.en}
                      </Link >
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    )
  }
}