import React, { Component } from "react";
import { Table } from "reactstrap";

export default class ShipProfile extends Component {
  render() {
    // Main
    let shipData  = this.props.shipData;

    // Metadata
    const wikiUrl          = shipData.wikiUrl;
    const id               = shipData.id;
    const name             = shipData.names.en;
    const classType        = shipData.class;
    const nationality      = shipData.nationality;
    const hullType         = shipData.hullType;
    const thumbnail        = shipData.thumbnail;
    const rarity           = shipData.rarity;
    const stars            = shipData.stars;
    const constructionTime = shipData.construction.constructionTime;
    const obtainedFrom     = shipData.obtainedFrom.obtainedFrom;


    // Metadata with Child
    // exist child        => {en, cn, jp, kr}                            (type: string)
    // availableIn child  => {light, heavy, aviation, limited, exchange} (type: bool, string)
    // enhanceValue child => {firepower, torpedo, aviation, reload}      (type: number) 
    // scrapValue child   => {coin, oil, medal}                          (type: number)
    const exist            = shipData.exists;
    const availableIn      = shipData.construction.availableIn;
    const enhanceValue     = shipData.enhanceValue;
    const scrapValue       = shipData.scrapValue;


    // Fleet Technology
    const fleetTech        = shipData.fleetTech;
    // statsBonus child => null || {applicable(multiple array[]), bonus, stat} (type: string or null) 
    const collection       = fleetTech.statsBonus.collection;
    const maxLevel         = fleetTech.statsBonus.maxLevel;
    // techPoints child => {collection, maxLimitBreak, maxLevel, total}
    const techPoints       = fleetTech.techPoints;


    // Slots child        => {multiple array[]} (type: 4 number, 1 string)
    // Slots[array] child => {maxEfficiency, minEfficiency, type, max, kaiEfficiency} (type: 4 number, 1 string)
    const slots = shipData.slots;


    // Skin
    const skinName       = shipData.skins[0].name;
    const skinImage      = shipData.skins[0].image;
    const skinBackground = shipData.skins[0].background;
    const skinChibi      = shipData.skins[0].chibi;
    // skinInfo child => {live2d, obtainedFrom} (type: bool, string)
    const skinInfo       = shipData.skins[0].info;


    // Stats child => (type: string)
    // health, armor, reload, luck, firepower, torpedo, evasion,
    // speed, antiair, aviation, oilConsumption, accuracy, antisubmarineWarfare
    const baseStats  = shipData.stats.baseStats;
    const level100   = shipData.stats.level100;
    const level120   = shipData.stats.level120;
    const level125   = shipData.stats.level125;


    // LimitBreaks & devLevels 
    let limitBreaks = true;
    // LimitBreaks
    if(limitBreaks) {
      // limitBreaks child => {multiple array[]} (type: string)
      const limitBreaks1 = shipData.limitBreaks[0];
      const limitBreaks2 = shipData.limitBreaks[1];
      const limitBreaks3 = shipData.limitBreaks[2];
    }


    // devLevels
    if(!limitBreaks) {
      // devLevels child => {level, buffs(multiple array[])} (type: string)
      const devLevel5  = shipData.devLevels[0];
      const devLevel10 = shipData.devLevels[1];
      const devLevel15 = shipData.devLevels[2];
      const devLevel20 = shipData.devLevels[3];
      const devLevel25 = shipData.devLevels[4];
      const devLevel30 = shipData.devLevels[5];
    }


    // Skills child              => {multiple array[]} (type: string)
    // Skills[Array] child       => {icon, names, description, color} (type: string)
    // Skills[Array].names child => {en, cn, jp} (type: string)
    const skills = shipData.skills;


    // Retrofit
    let retrofit = false;
    if(shipData.retrofit === true) {retrofit = true};

    let retrofitId        = "";
    let retrofitHullType  = "";
    let level100Retrofit  = "";
    let level120Retrofit  = "";

    if(retrofit === true) {
      retrofitId        = shipData.retrofitId;
      retrofitHullType  = shipData.retrofitHullType;
      level100Retrofit  = shipData.stats.level100Retrofit;
      level120Retrofit  = shipData.stats.level120Retrofit;
    }

    return (
      <div className="wrapper">
        <div className="page-header shipheader">
          <div className="content shipContent">
            <h1>{name}</h1>
            <div>
              <Table>
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>{id}</th>
                  </tr>
                  <tr>
                    <th>Class</th>
                    <th>{classType}</th>
                  </tr>
                  <tr>
                    <th>Faction</th>
                    <th>{nationality}</th>
                  </tr>
                  <tr>
                    <th>Classification</th>
                    <th>{hullType}</th>
                  </tr>
                  <tr>
                    <th>Rarity</th>
                    <th>{rarity}</th>
                  </tr>
                  <tr>
                    <th>Construction</th>
                    <th>{constructionTime}</th>
                  </tr>
                </thead>
              </Table>
            </div>
            <div>
              <Table>
                <thead>
                  <tr>
                    <th>{obtainedFrom}</th>
                  </tr>
                </thead>
              </Table>
            </div>
            <div>
              <Table>
                <thead>
                  <tr>
                    <th>Stats</th>
                    <th>baseStats</th>
                    <th>level100</th>
                    <th>level120</th>
                    <th>level125</th>
                  </tr>
                  <tr>
                    <th>health</th>
                    <th>{baseStats.health}</th>
                    <th>{level100.health}</th>
                    <th>{level120.health}</th>
                    <th>{level125.health}</th>
                  </tr>
                  <tr>
                    <th>armor</th>
                    <th>{baseStats.armor}</th>
                    <th>{level100.armor}</th>
                    <th>{level120.armor}</th>
                    <th>{level125.armor}</th>
                  </tr>
                  <tr>
                    <th>reload</th>
                    <th>{baseStats.reload}</th>
                    <th>{level100.reload}</th>
                    <th>{level120.reload}</th>
                    <th>{level125.reload}</th>
                  </tr>
                  <tr>
                    <th>luck</th>
                    <th>{baseStats.luck}</th>
                    <th>{level100.luck}</th>
                    <th>{level120.luck}</th>
                    <th>{level125.luck}</th>
                  </tr>
                  <tr>
                    <th>firepower</th>
                    <th>{baseStats.firepower}</th>
                    <th>{level100.firepower}</th>
                    <th>{level120.firepower}</th>
                    <th>{level125.firepower}</th>
                  </tr>
                  <tr>
                    <th>torpedo</th>
                    <th>{baseStats.torpedo}</th>
                    <th>{level100.torpedo}</th>
                    <th>{level120.torpedo}</th>
                    <th>{level125.torpedo}</th>
                  </tr>
                  <tr>
                    <th>evasion</th>
                    <th>{baseStats.evasion}</th>
                    <th>{level100.evasion}</th>
                    <th>{level120.evasion}</th>
                    <th>{level125.evasion}</th>
                  </tr>
                  <tr>
                    <th>speed</th>
                    <th>{baseStats.speed}</th>
                    <th>{level100.speed}</th>
                    <th>{level120.speed}</th>
                    <th>{level125.speed}</th>
                  </tr>
                  <tr>
                    <th>antiair</th>
                    <th>{baseStats.antiair}</th>
                    <th>{level100.antiair}</th>
                    <th>{level120.antiair}</th>
                    <th>{level125.antiair}</th>
                  </tr>
                  <tr>
                    <th>aviation</th>
                    <th>{baseStats.aviation}</th>
                    <th>{level100.aviation}</th>
                    <th>{level120.aviation}</th>
                    <th>{level125.aviation}</th>
                  </tr>
                  <tr>
                    <th>oilConsumption</th>
                    <th>{baseStats.oilConsumption}</th>
                    <th>{level100.oilConsumption}</th>
                    <th>{level120.oilConsumption}</th>
                    <th>{level125.oilConsumption}</th>
                  </tr>
                  <tr>
                    <th>accuracy</th>
                    <th>{baseStats.accuracy}</th>
                    <th>{level100.accuracy}</th>
                    <th>{level120.accuracy}</th>
                    <th>{level125.accuracy}</th>
                  </tr>
                  <tr>
                    <th>antisubmarineWarfare</th>
                    <th>{baseStats.antisubmarineWarfare}</th>
                    <th>{level100.antisubmarineWarfare}</th>
                    <th>{level120.antisubmarineWarfare}</th>
                    <th>{level125.antisubmarineWarfare}</th>
                  </tr>
                </thead>
              </Table>
            </div>
            <div>
              <Table>
                <thead>
                  <tr>
                    <th colSpan="3">Equipment Slot</th>
                  </tr>
                  <tr>
                    <th>Equipment</th>
                    <th>Efficiency</th>
                    <th>Mount</th>
                  </tr>
                </thead>
                <tbody>
                  {shipData.slots.map((slot, key) => {
                    const minEfficiency = slot.minEfficiency + "%"
                    const maxEfficiency = slot.maxEfficiency + "%"
                    let kaiEfficiency = "";
                    if(slot.kaiEfficiency) {kaiEfficiency = " → " + slot.kaiEfficiency + "%"}
                    const slotType = slot.type;
                    const slotEfficiency = minEfficiency + " → " + maxEfficiency + kaiEfficiency
                    const slotMax = slot.max;
                    return (
                      <tr key={key}>
                        <th>{slotType}</th>
                        <th>{slotEfficiency}</th>
                        <th>{slotMax}</th>
                      </tr>
                    )
                  })}
                </tbody>
              </Table>
            </div>
          </div>
        </div>
      </div>
    );
  }
}