import React, { Component } from "react";

import ShipProfile from "views/components/Ship/ShipProfile";
import ShipFetch from "views/components/Ship/ShipFetch";

export default class ShipRoute extends Component {
  render(){
  	const shipData = this.props.shipData;
  	if(shipData === undefined) {
  	  return <ShipFetch />;
  	} else {
      return <ShipProfile shipData={shipData} />
  	}
  }
}